header {
    height: 100vh;
    padding-top: 3rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* SocialIcons */

.header__socialicons {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    position: absolute;
    left: 0;
    bottom: 3.5rem;

}

.header__socialicons::after {
    content: '';
    width: 1px;
    height: 3rem;
    background: var(--color-primary);

}

/* Picture */
.me {
    width: 40rem;
    height: 26rem;
    position: absolute;
    left: calc(50% - 20rem);
    margin-top: 2rem;
    /* margin-bottom: 1rem; */
    /* border: 2px solid red; */
    border-radius: 3rem;
    overflow: hidden;
    padding: 0rem;

}

.scroll__down {
    position: absolute;
    right: -1.3rem;
    bottom: 3.5rem;
    transform: rotate(90deg);

}

/* Media queries MD Devices */
@media screen and (max-width: 1024px) {
    header {
        height: 100vh;

    }

    .me {
        width: 30rem;
        height: 18rem;
        position: absolute;
        left: calc(50% - 15rem);
        margin-top: 7rem;
        /* margin-bottom: 1rem; */
        /* border: 2px solid red; */
        border-radius: 3rem;
        overflow: hidden;
        padding: 0rem;

    }
}

/* Media queries SM Devices */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socialicons {
        display: block;
        position: relative;
        bottom: 0px;
        margin-top: 2rem;

    }

    .header__socialicons>a {
        padding: 1rem;
    }

    .scroll__down {
        display: none;
    }

    .me {
        width: 30rem;
        height: 18rem;
        position: absolute;
        left: calc(50% - 15rem);
        margin-top: 1rem;
        /* margin-bottom: 1rem; */
        /* border: 2px solid red; */
        border-radius: 3rem;
        overflow: hidden;
        padding: 0rem;

    }


}